
import Header from './components/Header';
import Auth from './components/Auth';
import UserProfile from './components/UserProfile'

import {useSelector } from 'react-redux';

function App() {
  const login = useSelector((state) => state.isLogin);
  return (
    <>
    <Header />
    {!login && <Auth/>}
    {login && <UserProfile  />}
    </>
  );
}

export default App;
